	.main-menu .navigation > li > ul > li > ul{
		display:block !important;
		visibility:hidden;
		opacity:0;
	}
}

@media only screen and (min-width: 1920px){
	.service-tab-section .tab-list-column .tab-list{
		margin-left: 30% !important;
    	margin-right: -30% !important;
	}
}
